<template>
  <!-- 维护字段 -->
  <div class="main_con_box">
    <div class="follow_list_box">
        <div class="follow_item"   :class="follow_active==item.id?'follow_active':''" v-for="item in followlist" :key="item.id">【{{item.label}}】</div>
    </div>
    <div class="follow_table_box">
        <div class="table_header">
            <el-button @click="newadd" type="primary" size="mini">新建</el-button>
        </div>
        <div class="table_box">
            <el-table :header-cell-style="{background:'#f5f5f5'}" :height="$store.state.innerheight - 125" :data="tableData" size="mini" border style="width: 100%">
                <el-table-column type="index" label="序号" width="80" />
                <el-table-column prop="name" label="名称" />
                <el-table-column prop="remark" label="备注" />
                <el-table-column label="操作">
                    <template #default="scope">
                        <div>
                            <el-button size="mini" type="text" @click="getedit(scope.row)">编辑</el-button>
                            <el-button size="mini" type="text" style="color:red">删除</el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </div>
    <detail ref="detail"></detail>
  </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, toRefs } from "vue";
import detail from './detail_alert.vue'
export default {
    components:{ detail },
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      followlist:[
          {label:'跟进管理-形式',id:1},
          {label:'跟进管理-客户异议',id:2},
          {label:'跟进管理-处理意见',id:3},
          {label:'客户管理-客户状态',id:4},
          {label:'客户管理-证照',id:5},
          {label:'订单合同-服务类型',id:6},
          {label:'回款管理-回款类型',id:7},
          {label:'产品管理-产品分类',id:8},
          {label:'产品管理-单位',id:9},
          {label:'产品管理-存储条件',id:10},
      ],
      follow_active:1,
      tableData:[
        {
          name: '电话',
          remark: 'Tom',
        },
        {
          name: '拜访',
          remark: 'Tom',
        },
      ],
    });

    onMounted(() => {});
    const newadd = () => {
        proxy.$refs.detail.changestatus()
    }
    const getedit=(res)=>{
       proxy.$refs.detail.editstatus(res)
    }
     
    return {
      ...toRefs(state),
      newadd,
      getedit,
    };
  },
};
</script>
<style scoped>
    .main_con_box{
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }
    .follow_list_box{
        height: 100%;
        width: 273px;
        background: #fff;
        margin-right: 10px;
        padding: 0 15px;
        box-sizing: border-box;
    }
    .follow_item{
        margin: 10px 0;
    }
    .follow_active{
        color: rgba(50, 150, 250, 0.996);
    }

    .follow_table_box{
        height: 100%;
        width: calc(100% - 283px);
        background: #fff;
        padding: 0 10px;
        box-sizing: border-box;
    }
    .table_header{
        text-align: right;
        padding: 10px;
        box-sizing: border-box;
    }
</style>
<style>
.main_con_box {
  height: 100%;
}
</style>